/*
    BASE: https://github.com/zeit/next.js#custom-error-handling
*/
import Layout from '../components/Layout';

class Error extends React.Component {
    static getInitialProps({ res, err }) {
      const statusCode = res ? res.statusCode : err ? err.statusCode : null
      return { statusCode }
    }
  
    render() {
      return (
        <Layout title="Oh no :(" heeader={false}>
            {
                statusCode === 404 ?
                <div className="message">
                    <img src="/static/404.jpg" alt="stream error"/>
                    <p><Link><a href="/">Previous page</a></Link></p>
                </div> : 
                <div className="message">
                    <h2>Oh no! ðŸ˜“</h2>
                    <p>Please try later</p>
                    <p><Link><a href="/">Go To Previous page</a></Link></p>
                </div>            
            }
            <p>
            {this.props.statusCode
                ? `An error ${this.props.statusCode} occurred on server`
                : 'An error occurred on client'}
            </p>
        </Layout>
      )
    }
  }
  
  export default Error